.card {
    position: relative;
    background: transparent;
    width: 300px;
    height: 300px;
    border: none;
  }
  
  .card:hover {
    width: 300px;
  }
  
  .card .container-image {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #ffffff;
    width: 190px;
    height: 190px;
    cursor: pointer;
    border: solid 4px #9113e1;
    outline: 5px solid black;
    border-radius: 50%;
    box-shadow: 0 0 3px 10px #9113e1, 2px 2px 3px #580c86, inset 2px 2px 2px #ffffff;
    transition: all .3s ease-in-out, opacity .3s;
    transition-delay: .6s, 0s;
  }
  
  .card:hover .container-image {
    opacity: 0;
    border-radius: 8px;
    transition-delay: 0s, .6s;
  }
  
  .card .container-image .image-circle {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 165px;
    height: auto;
    object-fit: contain;
    filter: drop-shadow(2px 2px 2px #1818188a);
    transition: all .3s ease-in-out;
    transition-delay: .4s;
  }
  
  .card:hover .container-image .image-circle {
    opacity: 0;
    transition-delay: 0s;
  }
  
  .card .content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #ffffff;
    padding: 20px;
    width: 190px;
    height: 190px;
    cursor: pointer;
    border: solid 4px #9113e1;
    outline: 5px solid black;
    border-radius: 8px;
    box-shadow: 0 0 3px 10px #9113e1, 2px 2px 3px #18181865, inset 2px 2px 2px #ffffff;
    visibility: hidden;
    transition: .3s ease-in-out;
    transition-delay: 0s;
    z-index: 1;
  }
  
  .card:hover .content {
    width: 290px;
    height: 190px;
    visibility: visible;
    transition-delay: .5s;
  }
  
  .card .content .detail {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: all .3s ease-in-out;
    transition-delay: 0s;
  }
  
  .card:hover .content .detail {
    color: #181818;
    opacity: 100%;
    transition: 1s;
    transition-delay: .3s;
  }
  
  .card .content .detail span {
    margin-bottom: 5px;
    font-size: 18px;
    font-weight: 800;
  }
  
  .card .content .detail button {
    background: #9113e1;
    margin-top: auto;
    width: 75px;
    height: 25px;
    color: #ffffff;
    font-size: 16px;
    border: solid 1px black;
    border-radius: 8px;
    transition: .3s ease-in-out;
  }
  
  .card .content .detail button:hover {
    background: #ffffff;
    border: solid 1px #9113e1;
    color:#9113e1;
    font: bold 90% ;
  }
  
  .card .content .product-image {
    position: relative;
    width: 100%;
    height: 100%;
  }
  
  .card .content .product-image .box-image {
    display: flex;
    position: absolute;
    top: 0;
    left: -25%;
    width: 100%;
    height: 115%;
    opacity: 0;
    transform: scale(.5);
    transition: all .5s ease-in-out;
    transition-delay: 0s;
  }
  
  .card:hover .content .product-image .box-image {
    top: -9px;
    left: -9px;
    opacity: 100%;
    transform: scale(1.6);
    transition-delay: .3s;
  }
  
  .card .content .product-image .box-image .img-product {
    margin: auto;
    width: 7rem;
    height: auto;
  }
  
  .fil-shoes1, .fil-shoes2 {
    fill: #333333
  }